// 全局变量
@primary-color: #1890ff;
@secondary-color: #fff;

// 全局样式
.layout {
  min-height: 100vh;
  
  .ant-layout-header {
    padding: 0 50px;
    
    .logo {
      float: left;
      width: 120px;
      height: 31px;
      margin: 16px 24px 16px 0;
      background: rgba(0, 0, 0, 0.2);
    }
  }
  
  .ant-layout-content {
    padding: 50px;
    
    .site-layout-content {
      padding: 24px;
      min-height: 280px;
    }
  }
  
  .ant-layout-footer {
    text-align: center;
  }
}

// 响应式设计
@media (max-width: 768px) {
  .layout {
    .ant-layout-header {
      padding: 0 20px;
    }
    
    .ant-layout-content {
      padding: 20px;
    }
  }
}
